.contactItem {
    padding-left: 10px;
}

.input {
    background-color: var(--blue);
    color: var(--ligthgreen);

}
.label {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    width:250px;
    justify-content: space-between;
    position: relative;

}

.errorField {
    color:red;
}
.label p {
    margin-right: 15px;
}

.form{
    display: flex;
    flex-direction: column;
}
.body {
    width: 100%;
    display: flex;
    border: 3px solid var(--black);
    padding: 3px;
    border-radius: 20px;

}
.text {
   min-width: 150px;
}

.input[type=textarea]{
    width: 100%;
    text-align: center;
    border-radius: 20px;
}

.message {
    position: absolute;
    right: -130px;
}