.avatar {
    border-radius: 10px;
    object-fit: cover;
    width: 300px;
    height: 300px;

}

.ava_block {
    position: relative;
    padding: 10px;
    margin-right: 30px;
    background-color: rgb(64, 13, 233);
    height: 320px;
    width: 320px;

}

.ava_block img {
    object-fit: cover;
}


.labelPhoto input {
    display: none;
}

.avatar:hover ~div label{
   z-index: 2;
}
.labelPhoto:hover {
    z-index: 2;
}

.labelPhoto {
    position: absolute;
    top:33px;
    left: 50%;
    transform: translate(-50%,   -50%);
    padding: 15px;
    background-color: rgb(2, 40, 2);
    color :var(--blue);
    font-size: 15px;
    font-weight: bold;
    border: 3px solid var(--ligthgreen);
    border-radius: 20px;
    cursor: pointer;
    z-index: -1;
}

.loaded {
    display: none;
}